import {
  CheckCircle2,
  Clock,
  MapPin,
  HeartPulse,
  Stethoscope,
  Truck,
  Phone,
  Shield,
} from "lucide-react"

export default function EmergencyFeatures() {
  return (
    <section className="bg-white py-16 text-gray-900 dark:bg-gray-900 dark:text-white">
      <div className="container mx-auto px-4">
        {/* Heading */}
        <div className="mb-12 text-center">
          <h2 className="mb-4 text-3xl font-bold md:text-4xl">
            Our Emergency Service Features
          </h2>
          <p className="mx-auto max-w-3xl text-lg text-gray-600 dark:text-gray-300">
            ONE HEALTHLINE CONNECT provides comprehensive emergency medical services
            designed to deliver rapid, professional care when you need it most.
          </p>
        </div>

        {/* Features Grid */}
        <div className="grid gap-8 md:grid-cols-2 lg:grid-cols-4">
          {[
            {
              icon: Clock,
              title: "Rapid Response",
              desc: "Our emergency teams aim to reach you within 5-15 minutes in urban areas and 15-30 minutes in rural locations.",
            },
            {
              icon: MapPin,
              title: "Nationwide Coverage",
              desc: "With emergency response units strategically located across Rwanda, we ensure help is always nearby.",
            },
            {
              icon: HeartPulse,
              title: "Advanced Life Support",
              desc: "Our ambulances are equipped with advanced life support equipment and staffed by trained paramedics.",
            },
            {
              icon: Stethoscope,
              title: "Medical Professionals",
              desc: "Our emergency teams include doctors, nurses, and paramedics trained in emergency medicine.",
            },
            {
              icon: Truck,
              title: "Modern Ambulance Fleet",
              desc: "Our fleet of modern ambulances is equipped with the latest medical technology for on-site treatment.",
            },
            {
              icon: Phone,
              title: "24/7 Dispatch Center",
              desc: "Our emergency dispatch center is staffed around the clock to coordinate rapid response to emergencies.",
            },
            {
              icon: Shield,
              title: "Hospital Network",
              desc: "Direct coordination with hospitals ensures seamless transfer and immediate care upon arrival.",
            },
            {
              icon: CheckCircle2,
              title: "Insurance Coordination",
              desc: "We work with all major insurance providers to ensure your emergency care is covered.",
            },
          ].map(({ icon: Icon, title, desc }, i) => (
            <div
              key={i}
              className="rounded-lg border border-gray-100 bg-white p-6 shadow-sm transition-all hover:shadow-md dark:border-gray-700 dark:bg-gray-800"
            >
              <div className="mb-4 inline-flex h-12 w-12 items-center justify-center rounded-full bg-red-100 text-red-600 dark:bg-red-900 dark:text-red-300">
                <Icon className="h-6 w-6" />
              </div>
              <h3 className="mb-3 text-xl font-semibold">{title}</h3>
              <p className="text-gray-600 dark:text-gray-300">{desc}</p>
            </div>
          ))}
        </div>
      </div>
    </section>
  )
}
