import { Heart, Brain, Bone, Thermometer, Droplets, Pill, LigatureIcon as Bandage, AlertTriangle } from "lucide-react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"

export default function EmergencyTypes() {
  return (
    <section className="bg-white dark:bg-gray-900 py-16 transition-colors">
      <div className="container mx-auto px-4">
        <div className="mb-12 text-center">
          <h2 className="mb-4 text-3xl font-bold text-gray-900 dark:text-white md:text-4xl">
            Emergency Situations We Handle
          </h2>
          <p className="mx-auto max-w-3xl text-lg text-gray-600 dark:text-gray-300">
            Our emergency teams are trained and equipped to respond to a wide range of medical emergencies across
            Rwanda.
          </p>
        </div>

        <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-4">
          {[
            { icon: Heart, title: "Cardiac Emergencies", desc: "Heart attacks, chest pain, cardiac arrest, and other heart-related emergencies requiring immediate attention." },
            { icon: Brain, title: "Neurological Emergencies", desc: "Strokes, seizures, severe headaches, and other neurological conditions requiring rapid response." },
            { icon: Bone, title: "Trauma & Injuries", desc: "Serious injuries from accidents, falls, or violence requiring immediate medical intervention." },
            { icon: Thermometer, title: "Respiratory Distress", desc: "Severe asthma attacks, difficulty breathing, choking, and other respiratory emergencies." },
            { icon: Droplets, title: "Severe Bleeding", desc: "Uncontrolled bleeding from injuries, wounds, or medical conditions requiring immediate treatment." },
            { icon: Pill, title: "Poisoning & Overdose", desc: "Accidental or intentional poisoning, drug overdoses, and adverse reactions requiring urgent care." },
            { icon: Bandage, title: "Burns & Scalds", desc: "Serious burns from fire, chemicals, electricity, or hot liquids requiring specialized treatment." },
            { icon: AlertTriangle, title: "Other Emergencies", desc: "Allergic reactions, diabetic emergencies, pregnancy complications, and other urgent medical situations." }
          ].map((item, idx) => (
            <Card key={idx} className="border-red-100 dark:border-red-300 dark:bg-gray-800">
              <CardHeader className="pb-2">
                <div className="mb-2 inline-flex h-10 w-10 items-center justify-center rounded-full bg-red-100 text-red-600 dark:bg-red-200 dark:text-red-800">
                  <item.icon className="h-5 w-5" />
                </div>
                <CardTitle className="text-xl text-gray-900 dark:text-white">{item.title}</CardTitle>
              </CardHeader>
              <CardContent>
                <CardDescription className="text-gray-600 dark:text-gray-300">
                  {item.desc}
                </CardDescription>
              </CardContent>
            </Card>
          ))}
        </div>

        <div className="mt-12 rounded-lg bg-red-50 dark:bg-red-900 p-6">
          <h3 className="mb-3 text-xl font-semibold text-gray-900 dark:text-white">When to Call Emergency Services</h3>
          <p className="mb-4 text-gray-700 dark:text-gray-300">
            Call our emergency number (912) immediately if you or someone around you experiences:
          </p>
          <ul className="grid gap-2 md:grid-cols-2 lg:grid-cols-3">
            {[
              "Chest pain or pressure",
              "Difficulty breathing",
              "Sudden weakness or numbness",
              "Severe bleeding",
              "Severe burns",
              "Poisoning or overdose",
              "Serious injuries",
              "Seizures",
              "Unconsciousness"
            ].map((text, idx) => (
              <li key={idx} className="flex items-center text-gray-700 dark:text-gray-300">
                <span className="mr-2 text-red-600 dark:text-red-400">•</span> {text}
              </li>
            ))}
          </ul>
        </div>
      </div>
    </section>
  )
}
